.green{
    color: green
}

.red{
    color: red;
}

.chip{
    width: 80%;
    margin: 10px;
}

.card-img {
    height: 100%;
    max-height: 250px;
    width: auto;
}

@media screen and (max-width: 576px) {
    .card-img {
        width: inherit;
        height: inherit;
        max-width: 50vw;
        max-height: unset;
    }
}