html body{
    width: 100%;
    padding: 0;
    margin: 0;
}

#root{
    height: 100%;
    width: 100%;
}

@keyframes color {
    0% {background-color: #26547C}
    20% {background-color: #06D6A0}
    40% {background-color: #EF476F}
    60% {background-color: #DD2C2C}
    80% {background-color: #53FF45}
    100% {background-color: #26547C}
}

@keyframes mono {
    0% {color: #26547C}
    20% {color: #06D6A0;}
    40% {color: #EF476F}
    60% {color: #DD2C2C}
    80% {color: #53FF45}
    100% {color: #26547C}
}

@keyframes wilde {
    0%{
        top: -5000px;
    }
    65%{
        top: 0px;
    }
    70%{
        top: -50px;
    }
    75%{
        top: -50px;
    }
    80%{
        top: 0px;
    }
    85%{
        top: -20px;
    }
    90%{
        top: 0px;
    }
    95%{
        top: -10px;
    }
    100%{
        top: 0px;
    }
}

a,
p,
h1,
h2,
h3,
h4,
h5,
h6,
small,
span {
  font-family: 'Montserrat', sans-serif;
}

#hr {
    display: block; 
    height: 1px;
    border: 0;
    border-top: 4px dashed #dc3545;
}

#wild{
    animation-name: wilde;
    animation-duration: 3s;
    animation-delay: 0s;
    animation-iteration-count: 1;
    animation-timing-function: linear;
}

.main-p{
    background-color: #8fc8d6;
    width: 100%;
    margin: 0;
}

#img{
    max-width: 40vw;
    margin: 40px;
}

#title{
    font-family: 'Fredoka One', cursive;
    color: #373737;
}

.left-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: relative;
}

.inicio-container {
    display: flex;
    flex-direction: column;
    width: 405px;
    margin-left: 50px;
    gap: 64px;
}

.color{
    animation: color 15s;
    animation-iteration-count: infinite;
    bottom: 0px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-right: 20px dashed #FEC601;
}

.mono{
    animation-name: mono;
    animation-duration: 15s;
    animation-iteration-count: infinite;
}

/*  */

.title-container {
    display: flex;
    padding: 16px;
    border-radius: 12px;
    background-color: #FFFF;
    width: fit-content;
    gap: 12px;
    box-shadow: 0 2px 15px #00000015;
}

.title-container div {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-left: 12px;
}

.title {
    font-family: 'Fredoka One', sans-serif;
    color: #8B5E3C;
    margin-bottom: .15rem;
}

.subtitle {
    font-family: 'Fredoka', sans-serif;
    color: #666666;
    margin: unset;
}

@media screen and (max-width: 576px) {
    .inicio-container {
        width: 100%;
        margin: unset;
    }

    .title-container {
        width: 100%;
        justify-content: space-around;
    }

    .name-container {
        position: absolute;
        top: 15px;
    }

    .title {
        font-family: 'Fredoka One', sans-serif;
        color: #373737;
    }
}